<template>
  <div class="tuijian">

		<section>
			
			<div class="item"  v-for="data in index">
				
				<router-link to="/wenda" tag="a">
					<div class="title">{{data.title}}</div>
				</router-link>
				
				<div class="tu">
					 <img :src="img"  alt="" v-for="img in data.images">
				</div>
				
				<div class="dantu">
					<img :src="data.imgs" alt="" />
				</div>
				
				<div class="info">
					<div v-show="data.biaoz" class="biaoz">{{data.biaoz}}</div>
					<div v-show="data.biaoa"  class="biaoa">{{data.biaoa}}</div>
					<div class="item-name">{{data.items}}</div>
					<div class="plun">{{data.plun}}</div>
					<div class="ctime">{{data.ctime}}</div>
					<div class="del">
						<span class="iconfont icon-guanbi1"></span>
					</div>
					
				</div>
			</div>	

		</section>
		
		<div class="kong1"></div>
		
  </div>
</template>

<script>
 //	引入 axios
	import axios from 'axios';
	
	export default {
	  name: 'Tuijian',
	  data () {
	    return {
	      index:[],
	    }
	  },
	  mounted(){
	  	// 通过ajax 获取数据
	  	axios.get('/static/data/index.json').then((data)=>{
	  		//设置数据
	  		this.index = data.data;
	  	})
	  }
	  
	}
</script>


<style scoped lang="scss">
	@import '../../assets/sass/base.scss';

	
	/* 主题内容   */
	section{
	    /*width:rem(990px);*/
	    width:100%;
	    height:auto;
	    margin:0 auto;
	    .item{
	        width:90%;
	        margin:0 auto;
	        margin-top:rem(50px);
	        padding-bottom: rem(60px);
	        border-bottom:1px solid #ccc; 
	        a{
	            color:#000;
	            -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
	            -webkit-user-select: none;      
	        } 
	       .title{
	          font-size: rem(60px);
	          line-height:rem(76px); 
	       } 
	        .tu{
	           width:100%;
	           padding-top: rem(30px);
	           img{
	             width:31%;
	             float:left;
	             padding:0 rem(10px);
	           } 
	           .bigimg{
		            width:100%;
	           }
	        }
	        .dantu{
	           width:100%;
	           /*padding-top: rem(30px);*/
	            img{
	            width:100%;
	           }   
	        }
	       .info{
	           clear: both;
	           font-size: rem(44px);
	           line-height: rem(44px);
	           color:#a1a1a1;
	           padding-top: rem(30px);
	           div{
	               display: inline-block;
	           }
	           .biaoz{
	               width:rem(100px);
	               height: rem(44px);
	               border:1px solid red;
	               border-radius: rem(10px);
	               color:red;
	               font-size:rem(40px);
	               text-align: center;
	               margin-right:rem(20px);
	           }
	           .biaoa{
	           		 width:rem(100px);
	               height: rem(44px);
	               border:1px solid #2a90d7;
	               border-radius: rem(10px);
	               color:#2a90d7;
	               font-size:rem(40px);
	               text-align: center;
	               margin-right:rem(20px);
	           }
	           .biaozr{
	           	width:rem(60px);
	           }
	           .biaozg{
	               color:#2a90d7;
	                 border:1px solid #2a90d7;
	           }
	           .item-name{
	              margin-right:rem(20px);
	           }
	           .plun{
	               margin-right:rem(20px); 
	           }
	           .ctime{
	               
	           }
	           .del{
	               float: right;
	               span{
	                   font-size: rem(70px);
	                  
	               }
	           }
	       }
	    }
	}
	
	
</style>



