<template>
  <div id="app">
    <router-view/>    
    	
			<footer>
				<ul>
					<router-link to="/" tag="li">
							<span class="iconfont" :class="$route.path=='/' ? 'icon-shouye1' : 'icon-shouye' "></span>
							<p :class="$route.path=='/' ? 'col' : 'pz'">首页</p>
					</router-link>
					
					<router-link to="/shipin" tag="li">
							<span class="iconfont" :class="$route.path=='/shipin' ? 'icon-bofang2' : 'icon-bofang'"></span>
							<p :class="$route.path=='/shipin' ? 'col' : 'pz'">西瓜视频</p>
					</router-link>
		
					<router-link to="/weitoutiao" tag="li">
							<span class="iconfont" :class="$route.path=='/weitoutiao' ? 'icon-xinxi' : 'icon-xinxi2'"></span>
							<p :class="$route.path=='/weitoutiao' ? 'col' : 'pz'">微头条</p>
					</router-link>
					
					<router-link to="/login" tag="li">
							<span class="iconfont" :class="$route.path=='/login' ? 'icon-dengluzhanghao' : 'icon-hollowow'"></span>
							<p :class="$route.path=='/login' ? 'col' : 'pz'">未登录</p>
					</router-link>
				</ul>
			</footer>
  </div>
</template>

<script>
	export default {
	  name: 'app'
	}
</script>

<style scoped lang="scss">
@import './assets/sass/base.scss';

	/* 底部  */
	footer{
	    width:100%;
	    /*height:rem(132px);*/
	    background: #f9f9f9;
	    padding-top:rem(15px);
	    position: fixed;
	    bottom:0;
	    left:0;
	    ul{
	        display: flex;
	        text-align: center;
	        li{
	            text-align: center;
	            width:25%;         
	            /*a{*/
	                color: #505050;
	                display:block;
	                -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
	                -webkit-user-select: none;
	                .icon-shouye1{
	                  font-size: rem(70px); 
	                  color: #f85959; 
	                }
	                 .icon-shouye{
	                  font-size: rem(70px);  
	                }
	                .icon-bofang{
	                    font-size: rem(70px);
	                }
	                 .icon-bofang2{
	                    font-size: rem(70px);
	                    color: #f85959; 
	                }
	                .icon-xinxi2{
	                     font-size: rem(70px);
	                }
	                .icon-xinxi{
                     font-size: rem(70px);
                     color: #f85959;
	                }
	                .icon-hollowow{
	                   font-size: rem(70px);
	                }
	                .icon-dengluzhanghao{
                	   font-size: rem(70px);
                     color: #f85959;
	                }
	                p{
	                 text-align: center;
	                 font-size:rem(38px);  
	                }
	                .col{
	                	color: #f85959;
	                }
	                .pz{
	                	color:#000;
	                }
	            /*}*/
	          
	        }
	
	    }
	}
</style>



