import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/Index'
import Shipin from '@/components/Shipin'
import Weitoutiao from '@/components/Weitoutiao'
import Iogin from '@/components/Iogin'
import Wenda from '@/components/Wenda'
import Wenda2 from '@/components/Wenda2'


//加载头条主页二级路由

import Tuijian from '@/components/index/Tuijian'
import Qut from '@/components/index/Qut'
import Shehui from '@/components/index/Shehui'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children:[
        {
          path: '/',
          name:'Tuijian',
          component: Tuijian,
        },
         {
          path: '/index/qut',
          name:'Qut',
          component: Qut,
        },
         {
          path: '/index/shehui',
          name:'Shehui',
          component: Shehui,
        }
      ]
    },
    
    {
      path: '/shipin',
      name: 'Shipin',
      component: Shipin
    },
    
    {
      path: '/weitoutiao',
      name: 'Weitoutiao',
      component: Weitoutiao
    },
    
    {
      path: '/login',
      name: 'Iogin',
      component: Iogin
    },
    
    {
      path: '/wenda',
      name: 'Wenda',
      component: Wenda
    },
    
    {
      path: '/wenda2',
      name: 'Wenda2',
      component: Wenda2
    },
    

  ]
})



